import React, { useState, useEffect } from 'react';
import ArticleList from '../components/ArticleList';
import articleContent from './article-content';
import NotFoundPage from './NotFoundPage';
import CommentsList from '../components/CommentsList';
import UpVoteSection from '../components/UpVoteSection';
import AddCommentForm from '../components/AddCommentForm';

const ArticlePage = ({ match }) => {
    const name = match.params.name;
    const article = articleContent.find(article => article.name === name);

    //defining a state, and a function to change the state
    //argument pass in useState is the initial state
    const [articleInfo, setArticleInfo] = useState({ upvotes: 0, comments: []});

    //inside useEffect, annomynous function that gets call everytime componet
    //is rendered
    //useEffect renders not only when it's mount but also when 
    //the component updates
    //useEffect watch over the array, and whenever it changes
    //useEffect would be called again, since it's an empty array
    //basically tells useEffect to only update once when it's first load
    //useEffect does not allow the function to be async
    useEffect(() =>{
        const fetchData = async () =>{
            //need to put await in front of functions that's async
            const result = await fetch(`/api/articles/${name}`)
            const body = await result.json();
            setArticleInfo(body);
        }
        fetchData();
    },[name]);

    if (!article) return <NotFoundPage></NotFoundPage>

    const otherArticles = articleContent.filter(article=> article.name !== name);
    return (
        <>
        <h1>{article.title}</h1>
        <UpVoteSection 
            articleName = {article.name} 
            upvotes = {articleInfo.upvotes}
            setArticleInfo = {setArticleInfo}/>
        {article.content.map((paragraph, key) => (
            <p key={key}>{paragraph}</p>
        ))}
        <CommentsList comments={articleInfo.comments}/>
        <AddCommentForm articleName={name} setArticleInfo={setArticleInfo}/>
        <h3>Other Articles</h3>
        <ArticleList articles={otherArticles}></ArticleList>
        </>
    );
}

export default ArticlePage;